var canvas = document.getElementById('canvasX')
var context = canvas.getContext('2d')

var usingBrush = false // æ˜¯å¦ä½¿ç”¨åˆ·å­ï¼Œå·¦é”®ç‚¹å‡»å³å¼€å¯

var lastPoint = { 'x': undefined, 'y': undefined }  //ä¸¤ç‚¹ä¹‹é—´çš„ä¸Šä¸€ä¸ªç‚¹
var newPoint = { 'x': undefined, 'y': undefined } //ä¸¤ç‚¹ä¹‹é—´ä¸‹ä¸€ä¸ªç‚¹



autoSetCanvasSize(canvas) //è‡ªåŠ¨è®¾ç½®ç”»å¸ƒå¤§å°

context.fillStyle = 'white'
context.fillRect(0, 0, canvas.width, canvas.height)
// 
if (document.body.ontouchstart === undefined) {
    // pcè®¾å¤‡
    listenToMouse(canvas)
    } else {
    //è§¦å±è®¾å¤‡
    // æŒ‰ä¸‹æ‰‹æŒ‡
    canvas.ontouchstart = function (ev) {
        var x = ev.touches[0].clientX
        var y = ev.touches[0].clientY

        usingBrush = true

        if (usingBrush) {
            if (usingEraser) {
                // ä½¿ç”¨æ©¡çš®æ“¦
                context.fillStyle = 'white'
                context.fillRect(x - 5, y - 5, 10, 10)
            } else {
                drawCircle(x, y, 3)
                lastPoint = { 'x': x, 'y': y }
            }
        }
    }

    // ç§»åŠ¨æ‰‹æŒ‡
    canvas.ontouchmove = function (ev) {
        var x = ev.touches[0].clientX
        var y = ev.touches[0].clientY

        newPoint = { 'x': x, 'y': y }

        if (usingBrush) {
            if (usingEraser) {
                context.fillStyle = 'white'
                context.fillRect(x - 5, y - 5, 10, 10)
            }
            else {
                drawLine(lastPoint.x, lastPoint.y, newPoint.x, newPoint.y)
                lastPoint = newPoint
                // å½“å¯¹è±¡ä¹‹é—´ä½¿ç”¨=çš„æ—¶å€™ï¼Œèµ‹å€¼çš„æ—¶å€™è¦å°å¿ƒï¼Œ
                // å¦‚æœæ˜¯.èµ‹å€¼ï¼Œåˆ™ä¸¤ä¸ªå¯¹è±¡ä¼šä¸€èµ·æ”¹å˜
                // å¦‚æœæ˜¯{}èµ‹å€¼ï¼Œåˆ™ç›¸äº’ä¹‹é—´ä¸ä¼šæ”¹å˜
            }
        }
    }

    //æ¾å¼€é¼ æ ‡
    canvas.ontouchend = function (ev) {
        usingBrush = false
    }
}

function listenToMouse(canvas) {
    //æŒ‰ä¸‹é¼ æ ‡
    canvas.onmousedown = function (ev) {
        var x = ev.clientX
        var y = ev.clientY

        usingBrush = true

        if (usingBrush) {
            if (usingEraser) {
                context.fillStyle = 'white'
                context.fillRect(x - 5, y - 5, 10, 10)
            } else {
                drawCircle(x, y, 3)
                lastPoint = { 'x': x, 'y': y }
            }
        }

    }


    //ç§»åŠ¨é¼ æ ‡
    canvas.onmousemove = function (ev) {
        var x = ev.clientX
        var y = ev.clientY

        newPoint = { 'x': x, 'y': y }

        if (usingBrush) {
            if (usingEraser) {
                context.fillStyle = 'white'
                context.fillRect(x - 5, y - 5, 10, 10)
            }
            else {
                drawLine(lastPoint.x, lastPoint.y, newPoint.x, newPoint.y)
                lastPoint = newPoint
                // å½“å¯¹è±¡ä¹‹é—´ä½¿ç”¨=çš„æ—¶å€™ï¼Œèµ‹å€¼çš„æ—¶å€™è¦å°å¿ƒï¼Œ
                // å¦‚æœæ˜¯.èµ‹å€¼ï¼Œåˆ™ä¸¤ä¸ªå¯¹è±¡ä¼šä¸€èµ·æ”¹å˜
                // å¦‚æœæ˜¯{}èµ‹å€¼ï¼Œåˆ™ç›¸äº’ä¹‹é—´ä¸ä¼šæ”¹å˜
            }
        }

    }

    //æ¾å¼€é¼ æ ‡
    canvas.onmouseup = function (ev) {
        usingBrush = false
    }

}


