    eraser.classList.add('active') //æ©¡çš®æ“¦è¢«ç‚¹å‡»æ—¶ï¼Œé«˜äº®
    brush.classList.remove('active') //å…¶ä»–æŒ‰é’®æ¢å¤æ­£å¸¸
    clear.classList.remove('active')
